import {Injectable} from "@angular/core";
import { distanceInWordsToNow, getTime, differenceInDays } from 'date-fns';
import * as itLocale from 'date-fns/locale/it/index.js';

@Injectable()
export class Timezone {

  constructor() {}

  getCurrentDate() {
    let date = new Date();
    let isoDate = new Date(date.getTime() - (date.getTimezoneOffset() * 60000)).toISOString();
    return isoDate;
  }

  getDistanceInWordsToNow(date) {
    return distanceInWordsToNow(date, {locale: itLocale, includeSeconds: true});
  }

  getDifferenceInDays(date) {
    return differenceInDays(
      new Date(),
      new Date(date)
    )
  }

  splitTimeZoneDate(current_date) {
    return current_date.split("T")[0];
  }

}



