import { Component, OnInit, OnDestroy, Input, AfterViewInit, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';
import { CommentService } from '../service/comment.service';

// Interface
import { Comment } from '../interface/comment';

@Component({
  selector: 'ticket-comments-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class TicketCommentAddComponent implements OnInit, AfterViewInit, OnDestroy {

  @Input() account;
  @Input() reporter;
  @Input() ticket_id: number;
  @Input() comments: Comment[];

  @ViewChild("buttons", {read: ElementRef}) buttons: ElementRef;
  @ViewChild("body_text", {read: ElementRef}) body_text: ElementRef;

  sub = new Subscription();
  loading: boolean = false;

  public editor_options: Object = {
    placeholderText: 'Inserisci commento',
    charCounterCount: false,
    toolbarSticky: false,
    toolbarButtons: ['bold', 'italic', 'underline', 'strikeThrough', '|', 'fontFamily', 'fontSize', 'color', 'paragraphStyle', '|', 'paragraphFormat', 'align', 'formatUL', 'outdent', 'indent', '|', 'quote', 'insertLink', 'insertHR', 'clearFormatting', '|', 'undo', 'redo'],
    //height: 300,
    theme: "gray",
    tooltips: true,
    events : {
      'froalaEditor.initialized' : function(e, editor) {
        editor.toolbar.hide();
      },
      'froalaEditor.focus' : function(e, editor) {
        editor.toolbar.show();
        editor.opts.height = 300;
        editor.size.refresh();
      }
      // 'froalaEditor.blur' : function(e, editor) {
      //   editor.toolbar.hide();
      // }
    }
  }

  constructor(
    private service: Service,
    private commentService: CommentService
  ) {}

  getLocationHeaders(data) {
    let patt = /(\d+)/g;
    let comment_id = data.headers.get("location");
    let comment_id_match = comment_id.match(patt);
    return +comment_id_match[0];
  }

  add(form) {
    this.loading = true;
    form.value.account_id = this.account.id;
    form.value.ticket_id = this.ticket_id;
    let subscription = this.service.create("comments", form.value).subscribe(
      data => {
        form.value.id = this.getLocationHeaders(data);
        form.value.created_at = null;
        form.value.updated_at = null;
        form.value.account = this.account;
        this.comments.push(form.value);
        this.loading = false;
        form.reset();
        this.hiddenToolbar();
        this.hiddenButtons();
      },
      err => {
        console.log(err);
        this.loading = false;
      },
      () => {
        this.commentService.changeComment(this.comments);
      }
    );

    this.sub.add(subscription);
  }

  revoke(event) {
    this.hiddenToolbar();
    this.hiddenButtons();
  }

  hiddenToolbar() {
    let body_text = this.body_text.nativeElement;
    $(body_text).froalaEditor('toolbar.hide');
  }

  hiddenButtons() {
    let buttons = this.buttons.nativeElement;
    buttons.style.display = 'none';
    // $(this.body_text.nativeElement).froalaEditor({
    //   height: 56
    // })
  }

  showButtons() {
    let buttons = this.buttons.nativeElement;
    buttons.style.display = 'block';
  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    let _self = this;
    this.hiddenButtons();
    $(this.body_text.nativeElement).on('froalaEditor.focus', function (e, editor) {
      _self.showButtons();
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



