import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../../services/http.service';
import { MessageService } from '../../../../services/message.service';
import { AccountService } from '../../service/account.service';

// Interfaces
import { Account } from '../../interface/account';

// Helpers
import { LocalStorageService } from '../../../../helpers/local-storage';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class AppSettingsComponent implements OnInit, OnDestroy {

  @Input() account: Account;

  sub = new Subscription();

  constructor(
    private service: Service,
    private accountService: AccountService,
    private messageService: MessageService,
    private lsService: LocalStorageService
  ) { }

  update(form) {
    let query = "id=eq." + this.account.settings[0].id;
    let subscription = this.service.update("profile_settings", query, form.value).subscribe(
      data => {
        this.messageService.setMessage("success");
        this.accountService.setLoggedAccount(this.account);
      },
      err => {
        console.log(err);
        this.messageService.setMessage("rejected");
      }
    );

    this.sub.add(subscription);
  }

  revoke(){
    this.account = JSON.parse(this.lsService.get("account"));
  }

  ngOnInit() {}

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



