import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from "rxjs/Subscription";

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';

// Interface
import { Ticket } from '../interface/ticket';
import { Account } from '../../accounts/interface/account';

// Helpers
import { LocalStorageService } from '../../../helpers/local-storage';

@Component({
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class TicketListComponent implements OnInit, OnDestroy {

  // account_id: number;
  // account_profile: number;
  account: any;
  accounts: Account[] = [];
  tickets: Ticket[] = [];

  sub = new Subscription();
  p: number = 1;

  constructor(
    private service: Service,
    private lsService: LocalStorageService,
    private accountService: AccountService
  ) {
    this.account = JSON.parse(this.lsService.get("account"));

    // this.accountService.logged_account.subscribe(
    //   (res: any) => {
    //     //console.log(res);
    //     this.account = res;
    //   }
    // );
    //this.account = this.accountService.logged_account;
    console.log(this.account);
    //this.account_id = this.accountService.logged_account;
    //this.account_profile = this.accountService.account_profile;

    this.loadAllItems("accounts", "accounts", "active_and_not_deleted");
  }

  get_tickets() {
    let query = "assignees_id=cs." + "{" + this.account.id + "}" + "&archived=" + "is.false" + "&order=" + "id.desc" + "&select=*, reporter: accounts(id, firstname, lastname)";
    let subscription = this.service.getRecords("tickets", query).subscribe(
      data => {
        let accounts = this.accounts;
        this.tickets = data;
        this.tickets.map(function(ticket) {
          ticket["accounts"] = [];
          ticket.assignees_id.forEach(function(account) {
            accounts.forEach(function(elem) {
              if(elem.id == account) {
                let obj = {
                  "id": elem.id,
                  "firstname": elem.firstname,
                  "lastname": elem.lastname,
                  "avatar_color": elem.avatar_color
                }
                ticket.accounts.push(obj)
              }
            })
          })
        });
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  loadAllItems(table, variable, condition) {
    this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );
  }

  ngOnInit() {
    this.get_tickets();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



