import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Account } from '../../../accounts/interface/account';

@Component({
  selector: 'card-team-members',
  templateUrl: './team-members.component.html',
  styleUrls: ['./team-members.component.scss']
})
export class CardTeamMembersComponent implements OnInit, AfterViewInit, OnDestroy {

  @ViewChild('list') list:ElementRef;
  accounts: Account[] = [];

  sub = new Subscription();

  constructor(
    private service: Service
  ) {
    this.loadAllItems("accounts", "accounts", "not_deleted");
  }

  loadAllItems(table, variable, condition) {
    let subscription = this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
  }

  ngAfterViewInit() {
    let list = this.list.nativeElement;
    $(list).niceScroll({
      cursorwidth:"5px",
      zindex: "auto",
      cursorcolor:"#7e7e7e"
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



