import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
// import { Subject } from 'rxjs/Subject';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

// Helpers
import { LocalStorageService } from '../helpers/local-storage';

@Injectable()
export class EventSourceListenerService {

  private myData = new BehaviorSubject<any>([]);
  account_id: number;
  url: string = "";

  constructor(
    private lsService: LocalStorageService
  ) {
    this.account_id = +this.lsService.get('accountId');
    this.url = 'http://' + window.location.hostname + ':4001/bracco/' + this.account_id + "/events";
  }

  connect(): void {
    const source = new window['EventSource'](this.url);
    source.addEventListener('message', event => {
      // console.log(JSON.parse(event.data))
      this.myData.next(JSON.parse(event.data));
    });
    source.addEventListener('open', event => {
      console.log("eventsource connected.")
			//addStatus('eventsource connected.')
		});
    source.addEventListener('error', event => {
			// if (event.eventPhase == EventSource.CLOSED) {
      //   console.log("eventsource was closed.")
			// }
      console.log("eventsource was closed.")
		});
  }

  // clearMessage() {
  //   this.myData.next('');
  // }

  getData(): Observable<any> {
    return this.myData.asObservable();
  }

}



