import { Component, OnInit } from '@angular/core';

// Services
import { MessageService } from '../../../services/message.service';
import { LocalStorageService } from '../../../helpers/local-storage';
import { AccountService } from '../../accounts/service/account.service';

@Component({
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent {

  account: any;

  constructor(
    private lsService: LocalStorageService,
    private messageService: MessageService,
    private accountService: AccountService,
  ) {
    this.sendMessage();
    this.account = JSON.parse(this.lsService.get("account"));
    // this.accountService.logged_account.subscribe(
    //   (res: any) => {
    //     this.account = res;
    //   }
    // );

  }

    sendMessage(): void {
      // send message to subscribers via observable subject
      this.messageService.setMessage('Message from Home Component to App Component!');
    }

    clearMessage(): void {
      // clear message
      this.messageService.clearMessage();
    }

}



