import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Addressbook } from '../../../address-book/interface/addressbook';

@Component({
  selector: 'card-addressbook-list',
  templateUrl: './addressbook-list.component.html',
  styleUrls: ['./addressbook-list.component.scss']
})
export class CardAddressbookListComponent implements OnInit, AfterViewInit, OnDestroy {

  customer_contacts: Addressbook[] = [];

  sub = new Subscription();
  @ViewChild('list') list:ElementRef;

  constructor(
    private service: Service
  ) { }

  ngOnInit() {
    let subscription = this.service.getAll("addressbook", "not_archived").subscribe(
      data => {
        this.customer_contacts = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngAfterViewInit() {
    let list = this.list.nativeElement;
    $(list).niceScroll({
      cursorwidth:"5px",
      zindex: "auto",
      cursorcolor:"#7e7e7e"
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



