import { Component, OnInit, OnDestroy } from '@angular/core';
import {NgForm} from '@angular/forms';
import { Subscription } from "rxjs/Subscription";

// Models
//import { Company } from '../_models/index';

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';

@Component({
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddingAddressBookComponent implements OnInit, OnDestroy {

  loading: boolean = false;
  sub = new Subscription();

  constructor(
    private service: Service,
    private messageService: MessageService
  ) { }

  add(form) {
    this.loading = true;
    let subscription = this.service.create("addressbook", form.value).subscribe(
      data => {
        this.loading = false;
        this.messageService.setMessage('success');
        form.reset();
      },
      err => {
        console.log(err);
        this.loading = false;
        this.messageService.setMessage('rejected');
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {}

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



