import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../services/http.service';
import { AccountService } from '../../accounts/service/account.service';

// Interfaces
import { Note } from '../../notes/interface/note';

// Helpers
import { Timezone } from '../../../helpers/timezone';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit, OnDestroy {

  @Input() account: any;
  @Input() notes: Note[] = [];

  sub = new Subscription();
  current_day: string = "";

  constructor(
    private service: Service,
    private accountService: AccountService,
    private timezoneHelper: Timezone
  ) {
    this.current_day = this.splitTimeZoneDate(this.timezoneHelper.getCurrentDate());
  }

  splitTimeZoneDate(current_date) {
    return current_date.split("T")[0];
  }

  getNotesCurrentDay() {
    let query = "account_id=eq." + this.account.id + "&archived=" + "is.false" + "&alarm_date=eq." + this.current_day;
    let subscription = this.service.getRecords("notes", query).subscribe(
      data => {
        this.notes = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  hideSidebar() {
    $("#aside").toggleClass("hide");
    $("#main-container").toggleClass("hide");
    $("#header").toggleClass("hide");
  }

  ngOnInit() {
    this.getNotesCurrentDay();
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



