import { Component, OnInit } from '@angular/core';

// Services
import { Service } from '../../../services/http.service';

// Interface
import { Addressbook } from '../interface/addressbook';

@Component({
  moduleId: module.id,
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class AddressbookListComponent implements OnInit {

  addressesbook: Addressbook[] = [];

  dataForRequestSearchComp = {
    "table": "addressbook",
    "parameters": ["firstname", "lastname", "company", "email", "mobilephone"],
    "condition": "archived=is.false"
  };

  p: number = 1;

  constructor(
    private service: Service
  ) {
    this.loadAllItems("addressbook", "addressesbook", "not_archived");
  }

  loadAllItems(table, variable, condition) {
    this.service.getAll(table, condition).subscribe(
      data => {
        this[variable] = data;
      }
    );
  }

  ngOnInit() {}

}



