import { Component, OnInit, OnDestroy, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { distanceInWordsToNow, addHours } from 'date-fns';
import * as itLocale from 'date-fns/locale/it/index.js';
declare var $:any;

// Services
import { Service } from '../../../../../services/http.service';
import { MessageService } from '../../../../../services/message.service';
import { AccountService } from '../../../../accounts/service/account.service';

// Helpers
import { Timezone } from '../../../../../helpers/timezone';
import { LocalStorageService } from '../../../../../helpers/local-storage';

@Component({
  selector: 'comment-list-row',
  templateUrl: './row.component.html',
  styleUrls: ['./row.component.scss']
})
export class CommentListRowComponent implements OnInit, OnDestroy {

  sub = new Subscription();

  @Input() comment;
  @Input() account;
  @Output() return_to_parent =  new EventEmitter();
  logged_account_id: number;
  private updated_at;

  public editor_options: Object = {
    charCounterCount: false,
    toolbarButtons: ['bold', 'italic', 'underline', 'strikeThrough', '|', 'fontFamily', 'fontSize', 'color', 'paragraphStyle', '|', 'paragraphFormat', 'align', 'formatUL', 'outdent', 'indent', '|', 'quote', 'insertLink', 'insertHR', 'clearFormatting', '|', 'undo', 'redo'],
    toolbarSticky: false,
    theme: "gray",
    tooltips: true,
    events : {
      'froalaEditor.initialized' : function(e, editor) {
        editor.toolbar.hide();
        editor.edit.off();
      }
    }
  }

  @ViewChild("body_text", {read: ElementRef}) body_text: ElementRef;

  constructor(
    private service: Service,
    public timezone: Timezone,
    private messageService: MessageService,
    private lsService: LocalStorageService,
    private accountService: AccountService
  ) {}

  getDistanceInWordsToNow(date) {
    return distanceInWordsToNow(date, {locale: itLocale});
  }

  getByIdCurrentComment(event) {
    let target = event.target || event.srcElement || event.currentTarget;
    let closest = target.closest(".media");
    let idAttr = closest.attributes.id;
    let idValue = idAttr.nodeValue;
    //let toolbar = (<HTMLElement>document.querySelector('#' + idValue + ' .fr-toolbar'));
    return idValue
  }

  edit(event) {
    let id = this.getByIdCurrentComment(event);

    let toolbar = (<HTMLElement>document.querySelector('#' + id + ' .fr-toolbar'));
    toolbar.style.display = "block";

    let textarea = (<HTMLElement>document.querySelector('#' + id + ' textarea'));
    $(textarea).froalaEditor("edit.on");
  }

  revoke(event) {
    let id = this.getByIdCurrentComment(event);

    let toolbar = (<HTMLElement>document.querySelector('#' + id + ' .fr-toolbar'));
    toolbar.style.display = "none";

    let textarea = (<HTMLElement>document.querySelector('#' + id + ' textarea'));
    $(textarea).froalaEditor("edit.off");
  }

  delete() {
    let subscription = this.service.delete("comments", "id", this.comment.id).subscribe(
      data => {
        this.return_to_parent.emit(this.comment);
      }
    );

    this.sub.add(subscription);
  }

  update(event) {
    let query = "id=eq." + this.comment.id;
    let date = new Date();
    //let date = this.timezone.getCurrentDate();

    // FIX: Reset value to pass to DB just because DB less 1 hour
    date = addHours(date, 1);
    // end

    let obj = {
      body_text: this.comment.body_text,
      updated_at: date
    }
    //console.log(obj);
    let subscription = this.service.update("comments", query, obj).subscribe(
      data => {
        obj.updated_at = new Date();
        this.comment.updated_at = this.getDistanceInWordsToNow(obj.updated_at);

        let id = this.getByIdCurrentComment(event);

        let toolbar = (<HTMLElement>document.querySelector('#' + id + ' .fr-toolbar'));
        toolbar.style.display = "none";

        let textarea = (<HTMLElement>document.querySelector('#' + id + ' textarea'));
        $(textarea).froalaEditor("edit.off");
      },
      err => {
        console.log(err)
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.logged_account_id = +this.lsService.get("accountId");
    if(this.comment.updated_at !== null) {
      this.comment.updated_at = this.getDistanceInWordsToNow(this.comment.updated_at);
    }
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



