import { Injectable } from '@angular/core';
import { Subject, BehaviorSubject } from 'rxjs';

@Injectable()
export class NoteService {

  notes: Subject<any> = new BehaviorSubject<any>([]);

  set(list) {
    this.notes.next(list);
  }

}



