import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';
import { AccountService } from '../../../accounts/service/account.service';

// Interface
import { Document } from '../../../documents/interface/document';

// Helpers
import { LocalStorageService } from '../../../../helpers/local-storage';

@Component({
  selector: 'card-document-list',
  templateUrl: './document-list.component.html',
  styleUrls: ['./document-list.component.scss']
})
export class CardDocumentListComponent implements OnInit, AfterViewInit, OnDestroy {

  documents: Document[] = [];

  sub = new Subscription();
  @ViewChild('list') list:ElementRef;

  constructor(
    private service: Service,
    private accountService: AccountService,
    private lsService: LocalStorageService
  ) { }

  ngOnInit() {
    const account_id = +this.lsService.get("accountId");
    let query = "share_with=cs." +"{" + account_id + "}" + "&" + "archived=" + "is.false" + "&select=*, account: accounts(firstname, lastname)";;
    let subscription = this.service.getRecords("documents", query).subscribe(
      data => {
        this.documents = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngAfterViewInit() {
    let list = this.list.nativeElement;
    $(list).niceScroll({
      cursorwidth:"5px",
      zindex: "auto",
      cursorcolor:"#7e7e7e"
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



