import { Component, OnInit, Input, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';
import { AccountService } from '../../../accounts/service/account.service';

// Interface
import { Note } from '../../../notes/interface/note';

@Component({
  selector: 'card-note-list',
  templateUrl: './note-list.component.html',
  styleUrls: ['./note-list.component.scss']
})
export class CardNoteListComponent implements OnInit, AfterViewInit, OnDestroy {

  notes: Note[] = [];

  @Input() account: any;

  sub = new Subscription();
  @ViewChild('list') list:ElementRef;

  constructor(
    private accountService: AccountService,
    private service: Service
  ) {}

  get_notes() {
    let query = "account_id=eq." + this.account.id + "&archived=" + "is.false";
    let subscription = this.service.getRecords("notes", query).subscribe(
      data => {
        this.notes = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    this.get_notes();
  }

  ngAfterViewInit() {
    let list = this.list.nativeElement;
    $(list).niceScroll({
      cursorwidth:"5px",
      zindex: "auto",
      cursorcolor:"#7e7e7e"
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



