import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import { Subscription } from "rxjs/Subscription";
declare var $:any;

// Services
import { Service } from '../../../../services/http.service';

// Interface
import { Ticket } from '../../../tickets/interface/ticket';

// Helpers
import { LocalStorageService } from '../../../../helpers/local-storage';

@Component({
  selector: 'card-ticket-list',
  templateUrl: './ticket-list.component.html',
  styleUrls: ['./ticket-list.component.scss']
})
export class CardTicketListComponent implements OnInit, AfterViewInit, OnDestroy {

  tickets: Ticket[] = [];

  sub = new Subscription();
  @ViewChild('list') list:ElementRef;

  constructor(
    private service: Service,
    private lsService: LocalStorageService
  ) { }

  ngOnInit() {
    const account_id = this.lsService.get('accountId');
    let query = "assignees_id=cs." + "{" + account_id + "}" + "&" + "archived=" + "is.false" + "&" + "status=neq." + "3" + "&select=*, reporter: accounts(firstname, lastname)";
    let subscription = this.service.getRecords("tickets", query).subscribe(
      data => {
        this.tickets = data;
      },
      err => {
        console.log(err);
      }
    );

    this.sub.add(subscription);
  }

  ngAfterViewInit() {
    let list = this.list.nativeElement;
    $(list).niceScroll({
      cursorwidth:"5px",
      zindex: "auto",
      cursorcolor:"#7e7e7e"
    });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



