import { Component, OnInit, OnDestroy, ViewChild, HostListener } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { Subscription } from "rxjs/Subscription";
import { FormCanDeactivate } from '../../../helpers/form-can-deactivate/form-can-deactivate';
import { NgForm } from "@angular/forms";

// Services
import { Service } from '../../../services/http.service';
import { MessageService } from '../../../services/message.service';
import { NoteService } from '../../notes/service/note.service';

// Interfaces
import { Note } from '../interface/note';

@Component({
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class NotesDetailsComponent extends FormCanDeactivate {

  id: number;
  note: any = {};
  notes: Note[] = [];

  sub = new Subscription();

  @ViewChild('form')
  form: NgForm;

  constructor(
    private service: Service,
    private noteService: NoteService,
    public route: ActivatedRoute,
    private messageService: MessageService
  ) {
    super();
  }

  update(data) {
    let query = "id=eq." + this.note.id;
    let subscription = this.service.update("notes", query, this.note).subscribe(
      data => {
        this.messageService.setMessage('success');
      },
      err => {
        console.log(err);
        this.messageService.setMessage('rejected');
      },
      () => {
        let note = this.note;
        this.notes = this.notes.map(function(item) {
          return item.id == note.id ? note : item;
        });
        this.noteService.set(this.notes);
      }
    );

    this.sub.add(subscription);
  }

  revoke(){
    this.getNote();
  }

  getNote() {
    let subscription = this.service.getBy("notes", "id", this.id).subscribe(
      data => {
        this.note = data;
      }
    );

    this.sub.add(subscription);
  }

  ngOnInit() {
    let subNotes = this.noteService.notes.subscribe(
      res => {
        this.notes = res;
      }
    );

    let subNote = this.route.params.subscribe(params => {
      this.id = +params['id']; // (+) converts string 'id' to a number
      this.getNote();
    });

    this.sub
        .add(subNotes)
        .add(subNote);
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }

}



