import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
declare var $:any;

// Services
import { EventSourceListenerService } from '../../../services/event-source-listener.service';

@Component({
  selector: 'notify-alert',
  templateUrl: './notify-alert.component.html',
  styleUrls: ['./notify-alert.component.scss']
})
export class NotifyAlertComponent implements OnDestroy, OnInit {

  @Input() account;

  bar_open: boolean = false;
  messages: any[] = [];
  data: any;
  subscription: Subscription;

  constructor(
    private eventSourceListenerService: EventSourceListenerService
  ) {
    this.subscription = this.eventSourceListenerService.getData().subscribe(
      data => {
        if(data.message !== undefined) {
          if(this.account.settings.tickets_notifications == true) {
            this.data = data.message.record;
            this.messages.push(this.switchMessage(data.message.record.id, data.message.operation, data.message.table));
            $(".notify-alert__btn").addClass("open");
          }
        }
      }
    );
  }

  hideSidebar() {
    this.bar_open = !this.bar_open;
    $(".notify-alert").toggleClass("hide");
  }

  deleteAllNotifications() {
    this.messages = [];
    $(".notify-alert__btn").removeClass("open");
  }

  switchMessage(id, operation, table) {
    let casestudies = {"DELETE": 1, "ARCHIVE": 2, "INSERT": 3, "UPDATE": 4};
    switch(casestudies[operation]) {
      case 1: {
        if(table == 'tickets') {
          return {
            "id": id,
            "text": "Il ticket TICKET-" + id + " è stato cancellato.",
            "table": table,
            "operation": operation
          }
        } else if(table == 'comments') {
          return {
            "id": id,
            "text": "Al ticket TICKET-" + this.data.ticket_id + " è stato eliminato un commento.",
            "table": table,
            "operation": operation
          }
        } else if(table == 'documents') {
          return {
            "id": id,
            "text": "Il document DOCUMENT-" + id + " è stato cancellato.",
            "table": table,
            "operation": operation
          }
        }
      };
      case 2: {
        if(table == 'tickets') {
          return {
            "id": id,
            "text": "Il ticket TICKET-" + id + " è stato archiviato.",
            "table": table,
            "operation": operation
          }
        } else if(table == 'documents') {
          return {
            "id": id,
            "text": "Il documento DOCUMNT-" + id + " è stato archiviato.",
            "table": table,
            "operation": operation
          }
        }
      };
      case 3: {
        if(table == 'tickets') {
          return {
            "id": id,
            "text": "Ti è stato assegnato un nuovo ticket",
            "table": table,
            "operation": operation
          }
        } else if(table == 'comments') {
          return {
            "id": id,
            "text": "E' stato inserito un nuovo commento al ticket " + "TICKET-" + this.data.ticket_id,
            "table": table,
            "operation": operation
          }
        }
      };
      case 4: {
        if(table == 'tickets') {
          return {
            "id": id,
            "text": "Il ticket TICKET-" + id + " è stato modificato.",
            "table": table,
            "operation": operation
          }
        } else if(table == 'comments') {
          return {
            "id": id,
            "text": "Al ticket TICKET-" + this.data.ticket_id + " è stato modificato un commento.",
            "table": table,
            "operation": operation
          }
        } else if(table == 'documents') {
          return {
            "id": id,
            "text": "Il documento DOCUMENT-" + id + " è stato modificato.",
            "table": table,
            "operation": operation
          }
        }
      };
    }
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  }

  ngOnInit() {}

}



